// src/components/BookingForm.jsx
"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { loadStripe } from "@stripe/stripe-js";

const stripePromise = loadStripe(process.env.NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY);

export default function BookingForm({ serviceId, priceId }) {
  const [date, setDate] = useState("");
  const [slots, setSlots] = useState([]);
  const [loading, setLoading] = useState(false);
  const [customer, setCustomer] = useState({ name: "", email: "", phone: "" });
  const router = useRouter();

  // 1) Cuando el usuario elige fecha, cargar slots
  useEffect(() => {
    if (!date) return;
    setLoading(true);
    fetch(`/api/easy?service_id=${serviceId}&date=${date}`)
      .then((r) => r.json())
      .then((data) => setSlots(data))
      .catch(console.error)
      .finally(() => setLoading(false));
  }, [date, serviceId]);

  // 2) Al enviar, primero crear sesión de Stripe
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!date || !customer.name || !customer.email || !customer.phone) return;

    const stripe = await stripePromise;
    // Creamos la sesión de pago enviando serviceId y priceId
    const res = await fetch("/api/stripe/checkout", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ serviceId, priceId, date, slot_id: e.target.slot.value }),
    });
    const { sessionId } = await res.json();
    // Redirigimos a Stripe Checkout
    const { error } = await stripe.redirectToCheckout({ sessionId });
    if (error) alert(error.message);
  };

  return (
    <form onSubmit={handleSubmit} className="card p-4 shadow-sm">
      <div className="mb-3">
        <label className="form-label">Fecha</label>
        <input
          type="date"
          className="form-control"
          value={date}
          onChange={(e) => setDate(e.target.value)}
          required
        />
      </div>

      {loading && <p>Cargando horarios…</p>}

      {slots.length > 0 && (
        <div className="mb-3">
          <label className="form-label">Horario</label>
          <select name="slot" className="form-select" required>
            {slots.map((s) => (
              <option key={s.id} value={s.id}>
                {s.start_time} – {s.end_time}
              </option>
            ))}
          </select>
        </div>
      )}

      <h5 className="mt-4">Tus datos</h5>
      <input
        type="text"
        className="form-control mb-2"
        placeholder="Nombre completo"
        value={customer.name}
        onChange={(e) => setCustomer({ ...customer, name: e.target.value })}
        required
      />
      <input
        type="email"
        className="form-control mb-2"
        placeholder="Correo electrónico"
        value={customer.email}
        onChange={(e) => setCustomer({ ...customer, email: e.target.value })}
        required
      />
      <input
        type="tel"
        className="form-control mb-2"
        placeholder="Teléfono"
        value={customer.phone}
        onChange={(e) => setCustomer({ ...customer, phone: e.target.value })}
        required
      />

      <button type="submit" className="btn btn-warning mt-3">
        Pagar y Agendar
      </button>
    </form>
  );
}
